/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* styles below */

html {
  background-color: rgb(62, 196, 196);
  color: rgb(20 31 58);
  font-family: 'Lato';
  text-align: center;
}

.top-image {
  background-image: url('./assets/images/proposal.jpg');
  background-size: contain;
  background-position: 0 -50px;
  background-repeat: no-repeat;
  width: 100%;
  height: 0;
  padding-top: 147.44%;
  position: relative;
  display: block;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 30px;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 48%, rgba(255, 255, 255, 0.7) 60%, rgba(255, 255, 255, 1) 75%);
    margin: 0 auto;
  }

  .title {
    position: absolute;
    z-index: 100;
    bottom: 20px;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 420px;

    &__names {
      font-size: 7.8rem;
      text-align: left;
      font-family: 'Dancing Script', cursive;
      color: rgb(62, 196, 196);
    }

    &__text {
      font-size: 1.85rem;
      text-align: center;
      font-family: 'Grape Nuts', cursive;
      margin: 8px 25px 0 0;
      color: rgb(21, 146, 146);
    }

    span {
      color: rgb(20 31 58);
    }
  }
}

.info {
  background-color: rgb(62, 196, 196);
  margin-top: 30px;
  padding: 50px 0 40px;
  font-family: 'Lato', sans-serif;
  line-height: 1.75;
  color: rgb(20 31 58);

  span {
    font-size: 1rem;
  }

  &__datetime {
    font-family: 'Noto Serif', serif;
    font-size: 3rem;
    font-weight: 700;
    letter-spacing: -1px;
    margin-bottom: -8px;
  }

  &__location {
    font-style: italic;
    font-size: 1.22rem;
    font-weight: 200;
    margin-bottom: 25px;
    letter-spacing: 1.2px;

  }

  &__text {
    font-size: 1.3rem;
    font-family: 'Indie Flower', cursive;
    margin: 35px 0 35px;
  }

  a,
  button {
    display: block;
    text-align: center;
    color: rgb(104 110 123);
    background-color: rgb(207 220 228);
    border-radius: 8px;
    padding: 10px 0;
    box-shadow: 5px 5px 6px rgb(175, 175, 175);
    font-weight: 300;
    font-size: 1.4rem;
    text-decoration: none;
    max-width: 460px;
    margin: 18px auto 0;

    &:hover {
      background-color: #ee6e9f;
    }
  }
}

button {
  border: none;
  width: 100%;
  font-weight: 300;
  font-family: 'Lato';
  line-height: 1.75;
}

@media (min-width:600px) {
  html {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 1) 28%, rgb(62, 196, 196) 65%);
  }

  .app {
    max-width: 800px;
    margin: 0 auto;
  }

  .top-image {
    width: 700px;
    height: 500px;
    margin-top: 20px;
    background-size: cover;
    background-position: 0 -140px;
    padding-top: 0;
    border-radius: 10px;

    &:after {
      top: 50px;
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 40%, rgba(255, 255, 255, 1) 75%);
      border-radius: 10px;
    }

    .title {
      bottom: 0px;
      left: 0;
      right: 0;
      margin-left: auto;
      margin-right: auto;
      width: 420px;

      &__text {
        margin: 8px 25px 0 0;
      }
    }
  }

  .info {
    background-color: rgba(255, 255, 255, 0);
  }
}